.skills {
    padding: 4rem 0;
    background-color: #1c1c3d;
    text-align: center;
}

.skills-wrapper {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 2rem;
}

.skills-column {
    flex: 1;
    max-width: 300px;
    background-color: #151538;
    padding: 1rem;
    border-radius: 8px;
}

.skills-column h3 {
    margin-bottom: 1rem;
    color: #7e22ce;
}

.skills-column ul {
    list-style: none;
}

.skills-column ul li {
    margin-bottom: 0.5rem;
}
